import { ReactiveVariable } from "vue/macros";
import { nextTick, Ref, ref } from "vue";
import { useStorePartner } from "@/store/partner";
import { FormFieldSelectProps } from "@/views/components/form/types";

export const useEvent = (isInit: Ref<boolean>) => {
  const storePartner = useStorePartner();

  // наполняем селект при бекенд поиске.
  async function onSelectSearch(filter: string, path: FormFieldSelectProps) {
    // modals.open(AvailableModalsEnum.LOADER);
    const pts = await storePartner.list({ search: filter });
    // modals.close();
    path.items = [];
    for (const row of pts.data) {
      path.items.push({
        value: String(row.partner_id),
        text: row.name,
      });
    }
  }

  return {
    onSelectSearch,
  };
};
