import { ReactiveVariable } from "vue/macros";
import { nextTick, Ref, ref } from "vue";
import {
  LacSelectOption,
  FormFieldSelectProps,
} from "@/views/components/form/types";
import { EventFormFields } from "@/views/modules/CrudForms/LacEventForm/use/useFields";
import { storeToRefs } from "pinia";
import { useStore } from "@/store";
import { useStoreAgent } from "@/store/agent";
import { useStorePartner } from "@/store/partner";
import { useStorePoint } from "@/store/point";

export const useUpdate = (isInit: Ref<boolean>) => {
  const storeAgent = useStoreAgent();
  const storePartner = useStorePartner();
  const storePoint = useStorePoint();
  const { agents } = storeToRefs(storeAgent);
  const { dictionary } = storeToRefs(useStore());
  const { partners, selectPartnerId } = storeToRefs(storePartner);

  async function updateAgent(
    path: FormFieldSelectProps,
    partnerId: number | undefined,
    agentId?: number
  ) {
    path.value = null;
    path.items = [];
    path.available = false;

    if (!partnerId) return;

    await storeAgent.getAgents({
      partner_id: partnerId,
    });

    const len = agents.value[partnerId].length || 0;

    if (len) {
      for (const row of agents.value[partnerId]) {
        const el = {
          value: String(row.agent_id),
          text: row.name,
        };
        path.items.push(el);
        if (agentId && agentId == row.agent_id) {
          path.value = el;
        }
      }
      if (len === 1) {
        path.value = path.items[0];
      } else {
        path.available = true;
      }
    }
  }

  async function updatePartner(
    path: ReactiveVariable<FormFieldSelectProps>,
    partnerId?: number | undefined
  ) {
    path.items = [];
    path.available = false;
    path.value = null;

    if (selectPartnerId.value) {
      path.items = [
        {
          value: String(selectPartnerId.value),
          text: partners.value[selectPartnerId.value].name,
        },
      ];
      path.value = path.items[0];
    } else {
      if (partnerId) {
        // вдруг значения нет в сторе.
        await storePartner.fetch(partnerId);
      }
      const pts = await storePartner.list({});
      if (pts.count) {
        let foundIndex = -1;
        for (const [index, row] of pts.data.entries()) {
          path.items.push({
            value: String(row.partner_id),
            text: row.name,
          });
          if (partnerId && row.partner_id == partnerId) {
            foundIndex = index;
          }
        }
        if (pts.count > 1) {
          if (partnerId) {
            if (foundIndex > -1) {
              path.value = path.items[foundIndex];
            } else {
              const el = {
                value: String(partnerId),
                text: partners.value[partnerId].name,
              };
              path.value = el;
              path.items.push(el);
            }
          }
          path.available = true;
        } else {
          path.value = path.items[0];
        }
      }
    }
  }

  async function updatePoint(
    path: ReactiveVariable<FormFieldSelectProps>,
    type: "offer" | "request",
    partnerId: number | undefined,
    pointId?: number
  ) {
    path.items = [];
    path.available = false;
    path.value = null;

    if (!partnerId) return;

    // вдруг стор по пунктам не наполнен.
    const points = storePoint.getPoints({
      partner_id: partnerId,
      noStore: true,
    });

    const point_subj = type === "offer" ? "subject_to_sell" : "subject_to_buy";

    const rawPoint = points!.filter((p) => p[point_subj].length);
    for (const row of rawPoint) {
      path.items.push({
        value: String(row.point_id),
        text: row.name,
      });
    }

    const len = path.items.length;

    if (len > 1) {
      if (pointId) {
        const index = rawPoint.findIndex((el) => el.point_id === pointId);
        path.value = path.items[index];
      }
      path.available = true;
    } else if (len == 1) {
      path.value = path.items[0];
    }
  }

  async function updateSubject(
    path: ReactiveVariable<FormFieldSelectProps>,
    type: string,
    pointId?: number,
    subject?: number
  ) {
    path.items = [];
    path.available = false;
    path.value = null;

    if (!pointId) return;

    const [point] = storePoint.getPoints({
      point_id: pointId,
      noStore: true,
    })!;

    const point_subj =
      type === "offer" ? point.subject_to_sell : point.subject_to_buy;

    const valToItems = [];
    for (const [index, subj] of point_subj.entries()) {
      path.items.push({
        value: subj,
        text: dictionary.value.point.subject[subj].desc,
        val: dictionary.value.point.subject[subj].val,
      });
      valToItems[dictionary.value.point.subject[subj].val] = index;
    }

    const len = path.items.length;

    if (len > 1) {
      path.available = true;
      if (subject) {
        path.value = path.items[valToItems[subject]];
      }
    } else if (len == 1) {
      await nextTick().then(() => {
        path.value = path.items[0];
      });
    }
  }

  async function updateFormula(
    path: ReactiveVariable<FormFieldSelectProps>,
    subject: string | undefined,
    formula?: string | undefined
  ) {
    path.items = [];
    path.value = null;
    path.available = false;

    console.log(path.value, subject, formula);

    if (!subject) return;

    const formulaDict = dictionary.value.offer.formula;
    const available_formula_by_subject =
      dictionary.value.offer.available_formula_by_subject;

    const items = [];
    const formulaToItems: any = [];
    for (const [index, row] of available_formula_by_subject[
      subject
    ].entries()) {
      items.push({
        value: row,
        text: formulaDict[row],
      });
      formulaToItems[row] = index;
    }

    path.items = items;
    path.available = true;

    await nextTick().then(() => {
      if (formula) {
        path.value = path.items[formulaToItems[formula]];
      } else {
        if (subject === "milk") {
          path.value = path.items[formulaToItems["standart"]];
        } else {
          path.value = path.items[formulaToItems["physical_weight"]];
        }
      }
    });

    const len = path.items.length;

    // len === 1 => never
    if (len > 1) {
      path.available = true;
    }
  }

  async function updateStatus(
    path: ReactiveVariable<FormFieldSelectProps>,
    type: "offer" | "request",
    status?: string | undefined
  ) {
    const items = [];
    const statusToItems: any = [];
    let index = 0;
    for (const key in dictionary.value[type]["status"]) {
      items.push({
        text: dictionary.value[type]["status"][key],
        value: key,
      });
      statusToItems[key] = index;
      index++;
    }

    if (!path.items.length) {
      path.items = items;
    }

    const fieldName = type === "offer" ? "ready_to_sell" : "ready_to_buy";

    path.value = status
      ? path.items[statusToItems[status]]
      : path.items[statusToItems[fieldName]];
  }

  return {
    updateAgent,
    updatePartner,
    updatePoint,
    updateSubject,
    updateFormula,
    updateStatus,
  };
};
